@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Montserrat:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}



.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    color: white;
    padding: .25rem 20px;
    box-sizing: border-box;
    position: relative;
    /*z-index: 1;*/
}

.header-start {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 300px;
}

.burger {
    position: fixed;
    z-index: 5;
}

/*Burger block*/

.burger-block {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    position: absolute;
    z-index: 5;
}

.burger-part {
    height: 1px;
    width: 30px;
    border-radius: 5px;
    transition: .4s;
}

.burger-block.active .burger-part:nth-child(1) {
    transform: translate(0, 8px) rotate(45deg);
}

.burger-block.active .burger-part:nth-child(2) {
    opacity: 0;
}

.burger-block.active .burger-part:nth-child(3) {
    transform: translate(0, -7px) rotate(-590deg);
}

/*Logo block*/

.logo-block {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.logo-title {
    font-size: 24px;
    font-weight: lighter;
    /*color: black;*/
}

.logo {
    width: 50px;
    margin-right: .5rem;
}

/*Data block*/

.date-block {
    font-size: 26px;
    margin-right: 2rem;
    font-weight: lighter;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.date-title {
    font-size: 26px;
    font-weight: lighter;
    letter-spacing: 3px;
}

/*Switcher block*/

.switcher-block {
    min-width: 200px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.switcher {
    background: #dbd224;
    border-radius: 20px;
    height: 20px;
    width: 50px;
    transition: .4s;
    position: relative;
    cursor: pointer;
}

.switcher-circle {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: white;
    transition: .4s;

    position: absolute;
    transform: translate(100%, -10%);
}

@media screen and (max-width: 768px) {
    .logo-title {
        opacity: 0;
        width: 0;
    }

    .logo-block {
        width: 100%;
        justify-content: flex-start;
        margin-left: 3rem;
    }

    .date-block {
        width: 100%;
        text-align: center;
        /*padding: 0 1rem;*/
        box-sizing: border-box;
    }
}

@media screen and (max-width: 425px) {
    .date-block {
        width: 100%;
        text-align: center;
        /*padding: 0 1rem;*/
        box-sizing: border-box;
        line-height: 1.5;
        margin-left: 1rem;
    }

    .date-title {
        width: 100%;
        font-size: 14px;
        margin: 0;
    }

    .switcher {
        transform: scale(.8);
    }

    .switcher-block {
        min-width: 150px;
    }

    .switcher-circle {
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background: white;
        transition: .4s;

        position: absolute;
        transform: translate(100%, -10%);
    }

    .switcher-dark, .switcher-light {
        font-size: 16px;
    }
}

