    padding-bottom: 2rem;
    /*box-sizing: border-box;*/
    /*height: 100%;*/
}

.search-title {
    font-size: 24px;
    font-weight: lighter;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin: 2rem 0;
}

.search-block {
    margin: 1rem auto 5rem;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 2rem;
    width: 100%;
}

.search {
    border-radius: 20px 20px 0 0;
    max-width: 900px;
    width: 100%;
    /*border: 2px solid black;*/
    display: flex;
    justify-content: space-between;
    align-items: center;
