    scroll-behavior: smooth;
}

.search-decor {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background: linear-gradient(to right, #7da8c9 ,#ffffff);
    position: absolute;
    top: 50%;
    left: 20%;
